*{
  margin: o;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: rgb(48, 143, 76);
    padding-bottom: 40px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.top{
  text-align: center;
  margin-top: 10%;
  margin-bottom: 5%;
  text-transform: capitalize;
}
.underline{
  margin: auto;
  border: 3px solid blue;
  width: 4%;
}
.tour1{
  width: 500px;
  background-color: aliceblue;
  padding-bottom: 20px;
  margin: auto;
  border-radius: 10px;
  box-shadow: 5px;
  margin-bottom: 30px;
}
footer{
  padding-left: 20px;
  padding-right: 20px;
}
img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.title h3{
  float: left;
}
.title .price{
  float: right;
  color: lightcoral;
}
p{
  display:inline-block;
}
.btn{
  width: 250px;
  margin-left:20%;
  border: 3px solid rgb(12, 219, 115);
  padding: 5px 5px;
  letter-spacing: 1px;
  font-size: 16px;
}
.loading{
  text-align: center;
}
.returnPage{
  margin-top: 20%;
text-align: center;
}
.ref-btn{
  width: 200px;
  border: none;
  background-color: rgb(194, 113, 21);
  letter-spacing: 1px;
  padding: 5px 5px;
  font-size: 16px;
  color: whitesmoke;
}
.pg-btn{
  color: rgb(26, 190, 240);
  border: none;
  letter-spacing: 1px;
  font-size: 18px;
  font-family:Arial, Helvetica, sans-serif;
  background-color: none;
}